<template>
  <div id="userManage">
    <QueryBox @searchSrldId="searchSrldId">
      <Button type="success" size="small" icon="md-add" class="btn-add" style="margin-right: 10px" @click.stop="openAdd">新建</Button>
    </QueryBox>
    <div class="tab_box">
      <Table ref="table" border stripe :loading="loading" :columns="columns1" :data="data1"  size="small" highlight-row :max-height="tableHeight"></Table>
    </div>
    <div class="page_box" id="pageBox">
      <Page :total="total" :page-size="pageSize" show-elevator @on-change="pageChange" size="small" show-total/>
    </div>
    <Modal v-model="modal1"
           :mask-closable="true"
           title="新建模板">
      <Form :model="formItem" ref="add" :rules="AddRule" :label-width="100">
        <FormItem label="模板名称" prop="name">
          <Input v-model="formItem.name" placeholder="请输入模板名称"></Input>
        </FormItem>
        <FormItem prop="compass" label="是否有指南针">
          <RadioGroup v-model="formItem.compass">
            <Radio label="1">是</Radio>
            <Radio label="0">否</Radio>
          </RadioGroup>
        </FormItem>
        <FormItem prop="scale" label="是否有比例尺">
          <RadioGroup v-model="formItem.scale">
            <Radio label="1">是</Radio>
            <Radio label="0">否</Radio>
          </RadioGroup>
        </FormItem>
      </Form>
      <div slot="footer">
        <Button type="primary" :loading="loading2" @click="addSubmit">确定</Button>
      </div>
    </Modal>
    <Modal v-model="modal2"
           :mask-closable="true"
           title="编辑流程">
      <Form :model="formItem1" ref="edit" :rules="EditRule" :label-width="100">
        <FormItem label="模板名称" prop="name">
          <Input v-model="formItem1.name" placeholder="请输入模板名称"></Input>
        </FormItem>
        <FormItem prop="compass" label="是否有指南针">
          <RadioGroup v-model="formItem1.compass">
            <Radio label="1">是</Radio>
            <Radio label="0">否</Radio>
          </RadioGroup>
        </FormItem>
        <FormItem prop="scale" label="是否有比例尺">
          <RadioGroup v-model="formItem1.scale">
            <Radio label="1">是</Radio>
            <Radio label="0">否</Radio>
          </RadioGroup>
        </FormItem>
      </Form>
      <div slot="footer">
        <Button type="primary" :loading="loading1" @click="submitUpdate">确定</Button>
      </div>
    </Modal>
  </div>
</template>

<script>
import { templateDelete, templatensert, templateUpdate, templateSelect } from '@/api/dataCenter'
import QueryBox from './template_query'
import { renderTime } from '@/utils/common'
export default {
  name: 'dataCenterLc',
  data () {
    return {
      /* 查询条件 */
      name: '',
      /* 表格动态高度 */
      tableHeight: window.innerHeight - 290,
      /* 每页条数 */
      pageSize: Math.floor((window.innerHeight - 290) / 40) - 1,
      /* 页码 */
      pageNum: 1,
      /* 页面总条数 */
      total: 0,
      /* 删除数据id */
      delId: '',
      /* 表格 */
      columns1: [
        {
          title: '序号',
          align: 'center',
          width: 100,
          render: (h, params) => {
            return h('span', params.index + (this.pageNum - 1) * this.pageSize + 1)
          }
        },
        {
          title: '模板名称',
          key: 'name',
          align: 'center'
        },
        {
          title: '是否有指南针',
          key: 'compass',
          render: (h, params) => {
            if (params.row.compass === 1) {
              return h('span', '有')
            } else if (params.row.compass === 0) {
              return h('span', '没有')
            }
          },
          align: 'center'
        },
        {
          title: '是否有比例尺',
          key: 'scale',
          render: (h, params) => {
            if (params.row.scale === 1) {
              return h('span', '有')
            } else if (params.row.scale === 0) {
              return h('span', '没有')
            }
          },
          align: 'center'
        },
        {
          title: '变更时间',
          key: 'updatetime',
          render: (h, params) => {
            return h('span', renderTime(params.row.updatetime))
          },
          align: 'center'
        },
        {
          title: '操作',
          key: 'action',
          width: 270,
          align: 'center',
          render: (h, params) => {
            return h('div', [
              h('Button', {
                props: {
                  type: 'warning',
                  size: 'small',
                  icon: 'md-create'
                },
                style: {
                  marginRight: '5px'
                },
                on: {
                  click: () => {
                    this.modal2 = true
                    this.formItem1.name = params.row.name
                    this.formItem1.id = params.row.id
                    this.formItem1.compass = String(params.row.compass)
                    this.formItem1.scale = String(params.row.scale)
                  }
                }
              }, '编辑'),
              h('Button', {
                props: {
                  type: 'error',
                  size: 'small',
                  icon: 'md-trash'
                },
                on: {
                  click: () => {
                    this.delId = params.row.id
                    this.$Modal.confirm({
                      title: '确认',
                      content: '请问您确定要删除该模板？',
                      onOk: () => {
                        this.sysDel()
                      },
                      onCancel: () => {
                      }
                    })
                  }
                }
              }, '删除')
            ])
          }
        }
      ],
      data1: [],
      /* 加载 */
      loading: false,
      /* 加载 */
      loading1: false,
      /* 加载 */
      loading2: false,
      // 控制模态框的展示
      modal1: false,
      // 添加表格数据
      formItem: {
        name: '',
        compass: '',
        scale: ''
      },
      AddRule: {
        name: [
          { required: true, message: '模板名称不能为空', trigger: 'blur' }
        ],
        compass: [
          { required: true, message: '请选择是否有指南针', trigger: 'change' }
        ],
        scale: [
          { required: true, message: '请选择是否有比例尺', trigger: 'change' }
        ]
      },
      // 修改表格数据
      formItem1: {
        id: '',
        name: '',
        compass: '',
        scale: ''
      },
      EditRule: {
        name: [
          { required: true, message: '模板名称不能为空', trigger: 'blur' }
        ],
        compass: [
          { required: true, message: '请选择是否有指南针', trigger: 'change' }
        ],
        scale: [
          { required: true, message: '请选择是否有比例尺', trigger: 'change' }
        ]
      },
      // 控制模态框的展示
      modal2: false
    }
  },
  components: {
    QueryBox
  },
  mounted () {
    /* 监听浏览器高度 */
    window.onresize = function () {
      this.tableHeight = window.innerHeight - 290
      this.pageSize = Math.floor((window.innerHeight - 290) / 40) - 1
    }.bind(this)
    this.getTableData()
  },
  methods: {
    /* axios: 获取列表数据 */
    getTableData () {
      this.loading = true
      templateSelect({
        data: {
          pageIndex: this.pageNum,
          pageSize: this.pageSize,
          name: this.name
        }
      }).then((res) => {
        if (res.data.code === '0000') {
          this.data1 = res.data.data.poList
          this.total = res.data.data.poSum
          this.loading = false
        } else {
          this.$Notice.error({ title: res.data.msg })
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    openAdd () {
      this.modal1 = true
      this.$refs.add.resetFields()
      this.formItem.name = ''
      this.formItem.compass = ''
      this.formItem.scale = ''
    },
    // 提交上传
    addSubmit () {
      this.$refs.add.validate(valid => {
        if (valid) {
          this.loading2 = true
          templatensert({
            data: {
              name: this.formItem.name,
              compass: this.formItem.compass,
              scale: this.formItem.scale
            }
          }).then(res => {
            this.loading2 = false
            this.modal1 = false
            if (res.data.code === '0000') {
              this.$Notice.success({ title: '新建模板成功' })
              this.getTableData()
            } else {
              this.$Notice.error({ title: '新建模板失败', desc: res.data.msg })
            }
          })
        }
      })
    },
    /* axios: 删除数据 */
    sysDel () {
      templateDelete({
        data: {
          id: this.delId
        }
      }).then((res) => {
        if (res.data.code === '0000') {
          this.getTableData()
          this.$Notice.success({ title: '删除成功' })
        } else {
          this.$Notice.error({ title: '删除失败' })
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    // 打开修改
    openUpdate () {
      this.modal2 = true
      this.$refs.edit.resetFields()
      this.formItem1.id = ''
      this.formItem1.name = ''
      this.formItem1.compass = ''
      this.formItem1.scale = ''
    },
    // 修改提交
    submitUpdate () {
      this.$refs.edit.validate(valid => {
        if (valid) {
          this.loading1 = true
          templateUpdate({
            data: {
              id: this.formItem1.id,
              name: this.formItem1.name,
              compass: this.formItem1.compass,
              scale: this.formItem1.scale
            }
          }).then(res => {
            this.loading1 = false
            this.modal2 = false
            if (res.data.code === '0000') {
              this.getTableData()
              this.$Notice.success({ title: '编辑成功' })
            } else {
              this.$Notice.error({ title: '编辑失败', desc: res.data.msg })
            }
          })
        }
      })
    },
    /* 查询框 */
    searchSrldId (obj) {
      this.name = obj.name
      this.pageNum = 1
      this.getTableData()
    },
    /* 分页跳转 */
    pageChange (p) {
      this.pageNum = p
      this.getTableData()
    }
  }
}
</script>

<style scoped lang="scss">
  #userManage{
    .from_box{
      padding:10px;
      text-align: left;
    }
    .tab_box{
      padding: 0 15px;
    }
    .page_box{
      width: 100%;
      padding: 15px;
      text-align: right
    }
    .btn-add {
      background-color: #01c296;
      border-color: #01c296;
      margin-left: 10px;
      &:hover, &:focus {
        background-color: #05ab85;
        border-color: #05ab85;
      }
    }
  }
</style>
