<template>
  <div class="textTerm">
    <span style="min-width: 100px;display: inline-block;text-align: right;padding-right: 10px;">{{titName}}：</span>
    <Input v-model.trim="UnitName" size="large" clearable :placeholder="'请输入' + titName" @on-change="srldChange(UnitName)" style="width: 200px;"/>
  </div>
</template>

<script>
export default {
  name: 'TextTerm',
  data () {
    return {
      /* input内容 */
      UnitName: ''
    }
  },
  props: {
    /* 字段标题 */
    titName: {},
    /* 字段key值 */
    titKey: {}
  },
  mounted () {

  },
  methods: {
    srldChange () {
      if (this.UnitName === null) {
        this.$emit('delQueryTrem', this.titKey)
      } else {
        let obj = {
          name: this.titName,
          text: this.UnitName,
          key: this.titKey,
          value: this.UnitName
        }
        this.$emit('updataQueryData', obj)
      }
    }
  }
}
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
  .cascadeTerm{

  }
</style>
